/* USER ACCOUNT */
#user-account {
  padding: 10px;
}

#user-account h1, #user-account h2, #user-account h3, #user-account h4 {
  font-family: Montserrat-Regular, sans-serif;
  text-transform: uppercase;
}

#user-account p {
