}

#user-account > h4 {
  margin: 10px 10px 20px;
}

#user-account #account-nav .account-settings, #user-account #account-nav .account-orders, #user-account #account-nav .account-order-history {
  position: relative;
  display: flex;
  height: 60px;
  font-size: 13px;
  font-weight: bold;
  text-transform: uppercase;
  border: 1px solid #eee;
  -webkit-transition: color ease-in-out 0.2s, border ease-in-out 0.2s, background ease-in-out 0.1s;
  transition: color ease-in-out 0.2s, border ease-in-out 0.2s, background ease-in-out 0.1s;
}

#user-account #account-nav .account-settings:hover, #user-account #account-nav .account-orders:hover, #user-account #account-nav .account-order-history:hover {
  border-color: #E6E6E6;
  background-color: #eee;
  box-shadow: inset #303030 0 1px 3px 0;
}

#user-account #account-nav .account-settings a, #user-account #account-nav .account-orders a, #user-account #account-nav .account-order-history a {
  display: flex;
  padding: 10px;
  width: 100%;
  height: 100%;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
}

#user-account #account-nav .account-settings a i, #user-account #account-nav .account-orders a i, #user-account #account-nav .account-order-history a i {
  padding-right: 7px;
}

#user-account #account-nav .active {
  color: #CC4040;
}

#user-account #account-nav .active:before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  left: 50%;
  bottom: -1px;
  border: 10px solid;
  border-color: transparent transparent #eee transparent;
}

#user-account #account-nav .active:after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  left: 50%;
  bottom: -3px;
  border: 10px solid;
  border-color: transparent transparent #fff transparent;
}

#user-account #account-nav .active:hover::before {
  border-color: transparent transparent #E6E6E6 transparent;
}

#user-account #account-panel {
  margin: 20px 0;
  padding: 10px;
  width: 100%;
  overflow-x: auto;
}

#user-account #account-panel > h4 {
  margin: 0 10px 20px;
