/* GENERAL */
body {
  font-family: "OpenSans-Regular", sans-serif;
  background-color: #fff;
  line-height: 1.2;
}

main {
  margin: 0 auto;
  padding-bottom: 50px;
  min-height: 1000px;
}

a {
  color: #0d0d0d;
}

a:hover, a:focus {
  color: #CC4040;
  text-decoration: none;
}

input[type=number] {
  -moz-appearance: textfield;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  margin: 0;
  -webkit-appearance: none;
}

/* HELPERS */
.text-center {
  text-align: center !important;
}

.cf:before,
.cf:after {
  content: "";
  display: table;
}

.cf:after {
  clear: both;
}

.cf {
  zoom: 1;
  /* ie 6/7 */
}

.no-wrap {
  white-space: nowrap;
}

table .col-xs {
  width: 50px;
}

table .col-sm {
  width: 80px;
}

table .col-md {
  width: 155px;
}

#wrapper {
  position: relative;
  margin: auto;
  max-width: 1920px;
}

.container-inner {
  margin: auto;
  padding: 0 5px;
  max-width: 1200px;
}

/* PAGINATION */
