  margin: 22px 10px;
  float: right;
}

.product-pagination .pagination a, .product-pagination .pagination a:hover {
  color: #0d0d0d;
}

.product-pagination .pagination .page-first, .product-pagination .pagination .page-last {
  font-weight: bold;
}

.product-pagination .pagination > li {
  display: inline-block;
  float: left;
}

/* HEADER */
#nav-top {
  background-color: #303030;
  border: 1px solid #222;
  border-radius: 0;
}

#nav-top a {
  color: #e0e0e0;
}

#nav-top a:hover {
  color: #CC4040;
}

#nav-top li:hover {
  background-color: #272323;
}

#nav-top li.dropdown.open a.dropdown-toggle {
  background-color: #CC4040;
}

#nav-top li.dropdown > a {
  color: #e0e0e0;
}

#nav-top ul.dropdown-menu {
  background-color: #CC4040;
  border-top: 1px solid #CC4040;
  border-radius: 0;
}

#nav-top ul.dropdown-menu li.divider {
  background-color: #0d0d0d;
}

#nav-top ul.dropdown-menu li > a:hover, #nav-top ul.dropdown-menu li > a:focus {
  background-color: #f37c34;
  color: #e0e0e0;
}

#nav-top .navbar-brand {
  padding: 0 15px;
}

.flash-message {
  margin: 10px auto;
  width: 50%;
}

.flash-message .message {
  display: inline-block;
  position: relative;
  padding: 20px 40px;
  width: 100%;
}

.flash-message .message-success {
  color: #3c763d;
  background-color: #dff0d8;
  border: 1px solid #3c763d;
}

.flash-message .message-info {
  color: #31708f;
  background-color: #d9edf7;
  border: 1px solid #31708f;
}

.flash-message .message-warning {
  color: #f37c34;
  background-color: #fff3eb;
  border: 1px solid #f37c34;
}

.flash-message .message-danger {
  color: #CC4040;
  background-color: #ffe7e7;
  border: 1px solid #CC4040;
}

.flash-message .message-text {
  word-wrap: break-word;
}

.flash-message .message-close {
  display: none;
  position: absolute;
  top: 10px;
  right: 10px;
}

.flash-message .message-close a, .flash-message .message-close a:hover {
  color: #303030;
  opacity: 0.5;
}

.flash-message .message-close a:hover {
  opacity: 0.8;
}

/* FOOTER */
footer {
  position: relative;
  bottom: 0;
  width: 100%;
  height: 50px;
  text-align: center;
  background-color: #0d0d0d;
  clear: both;
}

footer p {
  padding: 15px 0 0 0;
  margin: 0;
  color: #999999;
  font-size: 1em;
}
